<template>
  <head-bar page-title="登录" back-bar="true" page-link="back"></head-bar>
  <div class="hello">
    <div class="">
      <input type="text" name="name" value="" placeholder="username">
      <input type="password" name="name" value="" placeholder="password">
    </div>
    <button type="button" name="button" @click="login">登录</button>
  </div>
  <nav-bar></nav-bar>
</template>
<script>
export default {
  data () {
    let self = this
    return {
      alert: {
        txt: '',
        show: false,
        hideFn: function (ts) {
          let timer = ts || 2000
          setTimeout(function () {
            self.alert.show = false
          }, timer)
        }
      }
    }
  },
  components: {
    'navBar': require('../components/navBar.vue'),
    'headBar': require('../components/header.vue')
  },
  methods: {
    login () {
      let self = this
      window.localStorage.loginStatus = true
      let redirect = decodeURIComponent(self.$route.query.redirect || '/')
      self.alert.txt = '登录成功'
      self.alert.show = true
      setTimeout(function () {
        self.alert.hideFn()
        self.$route.router.go(redirect)
      }, 2000)
    }
  }
}

</script>
<style lang="stylus">
@import '../assets/styl/base'
</style>



